import { BindObject, observeInsert, observeRender } from "../../js/bind";
import juicer from "juicer";

import "./footer.scss";
// const compiledTpl = juicer(require("./footer.shtml"));
const compiledTpl = '';

export class Footer {
  data = {};
  containerId = "";

  constructor(id) {
    this.data = new BindObject(this);
    this.containerId = id;

    // 全局对象
    observeInsert(this);
  }

  init() {
    this.data.set({ msg: "123123123123", id: "856" });
    console.log("footer init");
  }

  viewDataChanged() {
    if (!this.containerId) {
      return;
    }
    observeRender(this, this.containerId, compiledTpl, this.data.get());
  }

  printInfo(v) {
    console.log("v", v);
  }
}
