import { Footer } from "./views/footer/footer.js";
import { observeInit } from "./js/bind.js";
import juicer from "juicer";


import "./main.scss";
// const compiledTpl = juicer(require("./main.shtml"));
const compiledTpl = '';

(function () {
  juicer.set({
    "tag::operationOpen": "{@", // each/for/if
    "tag::operationClose": "}",
    "tag::interpolateOpen": "${", // 变量值
    "tag::interpolateClose": "}",
    "tag::noneencodeOpen": "$${",
    "tag::noneencodeClose": "}",
    "tag::commentOpen": "{#", // 注释
    "tag::commentClose": "}",
  });

  observeInit();

  document.getElementById("app").innerHTML = compiledTpl.render({});;

  new Footer("footer").init();
})();
